.item {
  width: 100%;
  text-align: center;
  margin-bottom: 4rem;
  cursor: pointer;
  transition: background-color 500ms;
}

@media only screen and (min-width: 1024px) {
  .item {
    width: 32%;
    display: inline-block;
    margin-right: 1%;
  }
  .item:nth-child(3n) {
    margin-right: 0;
  }
}

@media only screen and (min-width: 1280px) {
  .item:hover {
    background-color: #e5e5e5;
  }
}

.item__image {
  background-color: #c0c0c0;
  width: 100%;
  position: relative;
  height: 0;
  padding-bottom: 130%;
  overflow: hidden;
}

.item__imageBg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  transform: scale(1);
  transition: transform 200ms;
}

.item__imageBg:hover {
  transform: scale(1.8);
  transition: transform 500ms;
}

.item__name {
  text-transform: capitalize;
  margin-top: 3rem;
}

.item__price {
  font-family: "open-sans-bold";
  margin-top: 1rem;
}

.item__color {
  margin-top: 1rem;
}

.item__color div {
  width: .8rem;
  height: .8rem;
  cursor: default;
}

@media only screen and (min-width: 1280px) {
  .item__color div:hover {
    border: none;
  }
}



